@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-color:#282c34;min-height:100vh}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}.flex-container{display:flex;flex-direction:column;align-items:center;justify-content:center;color:white}a{color:#61dafb}.app-container{display:flex;flex-direction:column;justify-content:flex-start;align-items:stretch}.main-container{margin-top:5rem}

.privacy-policy-container{width:800px;max-width:100vw}

.not-found-container h1{color:#EF5350}

.logo-container h1{margin:1rem;padding:0}.logo-container h1 a{font-family:'Lobster', cursive;color:#EF5350;text-decoration:none;transition:color 500ms, text-shadow 500ms;text-shadow:0 0 1.5rem #FF636044}.logo-container h1 a:hover{color:#FF6360;text-shadow:0 0 1.5rem  #ffffff88}

.header-nav-container{display:flex;flex-direction:row;justify-content:center;align-items:center}.header-nav-container a{color:white;font-weight:bolder;text-decoration:none;text-shadow:none;transition:text-shadow 200ms}.header-nav-container a:hover{text-shadow:0 0 1.5rem white}.header-nav-container__item:not(:last-child):after{content:"|";margin:1rem}.header-nav-container__item--active a{color:#61dafb}

.header-container{position:fixed;display:flex;flex-direction:row;justify-content:space-between;align-items:center;top:0;margin:0;padding:0;width:100%;min-height:5rem;max-height:5rem;background-color:#383c44;box-shadow:0rem -1rem 1rem #61dafb;color:white}

.home-games-container{display:flex;flex-direction:column;justify-content:flex-start;align-items:stretch;margin:auto;padding:1rem 1rem;width:60rem}

.product-container{margin:1rem;background-color:#383c44;border-radius:1rem;border:1px solid #282c24;box-shadow:0 0.1rem 1rem #383c44;padding:1rem}.product-container .left-panel,.product-container .right-panel,.product-container .spaced-panel{display:flex;flex-direction:row;align-items:stretch}.product-container .left-panel{justify-content:flex-start}.product-container .right-panel{justify-content:flex-start;flex-direction:row-reverse}.product-container .spaced-panel{justify-content:space-between}.product-container .product-title,.product-container .product-description,.product-container .product-screenshot{margin:0.5rem}.product-container .product-title{width:100%;display:flex;flex-direction:row;justify-content:flex-start;align-items:center;margin:1rem;padding:1rem;background-color:#383c44;border:1px solid #282c24;border-radius:1rem;box-shadow:0 0.1rem 1rem #383c44 inset,0 0.1rem 1rem #282c24}.product-container .product-title h2{margin:0;padding:0;color:white}.product-container .product-description{background-color:#484c54;border:1px solid #282c24;border-radius:1rem;padding:1rem;margin:1rem;box-shadow:0 0 1rem #282c24 inset}.product-container .product-description p{margin:1rem;color:white}.product-container .product-screenshot{margin:1rem}.product-container .product-screenshot img{border:1px solid #282c24;border-radius:1rem;width:15rem;height:auto;cursor:zoom-in}.product-container .product-screenshot .product-screenshot__modal{position:fixed;left:0;top:0;width:100vw;height:100vh;display:flex;justify-content:center;align-items:center;background-color:#000a;z-index:1040;cursor:zoom-out}.product-container .product-screenshot .product-screenshot__modal img{cursor:zoom-out;width:auto;height:auto;max-width:100vw;max-height:100vh;border:none;border-radius:0}.product-container .red .product-title{background-color:#FF6360}.product-container .blue .product-title{background-color:#61dafb}.product-container .blue .product-title h2{color:#383c44}

.home-apps-container{display:flex;flex-direction:column;justify-content:flex-start;align-items:stretch;margin:auto;padding:1rem 1rem;width:60rem}

.home-about-container{display:flex;flex-direction:column;justify-content:flex-start;align-items:stretch;margin:auto;padding:1rem 1rem;width:60rem}.home-about-container .about-panel{display:flex;flex-direction:column;justify-content:flex-start;align-items:stretch;margin:1rem;padding:1rem;background-color:#383c44;border:1px solid #282c24;border-radius:1rem;box-shadow:0 0.1rem 1rem #383c44 inset,0 0.1rem 1rem #282c24}.home-about-container .about-panel h2{margin:0.5rem;color:#FF6360}.home-about-container .about-panel p{margin:0.5rem;color:white}


.home-contact-container{display:flex;flex-direction:column;justify-content:flex-start;align-items:stretch;margin:auto;padding:1rem 1rem;width:40rem}.home-contact-container .contact-panel{display:flex;flex-direction:column;justify-content:flex-start;align-items:stretch;margin:1rem;padding:0.5rem;background-color:#383c44;border:1px solid #282c24;border-radius:1rem;box-shadow:0 0.1rem 1rem #383c44 inset,0 0.1rem 1rem #282c24}.home-contact-container .contact-panel h1{margin:0.5rem;color:#FF6360}.home-contact-container .contact-panel p{margin:0.5rem;color:white}.home-contact-container .contact-panel input[type=submit]{margin:0.5rem;padding:1rem 3rem;background-color:#FF6360;border:1px solid #282c24;border-radius:1rem;box-shadow:0 0.1rem 1rem #383c44 inset;color:white;font-size:larger;font-weight:bolder}.home-contact-container .contact-panel textarea{margin:0.5rem;padding:1rem;min-height:20rem;border:1px solid #282c24;border-radius:1rem;box-shadow:0 0.1rem 1rem #383c44 inset;color:#484c54;text-align:left}.home-contact-container .f-end{display:flex;flex-direction:row;justify-content:flex-end}

