$color-lightest-gray: #484c54;
$color-light-gray: #383c44;
$color-gray: #282c24;
$color-dark-gray: #181c14;
$color-shadow: #383c44;
$color-red: #FF6360;
$color-blue: #61dafb;
$radius-def: 1rem;
$border-def: 1px solid $color-gray;

.product-container {
    margin: 1rem;
    background-color: $color-light-gray;
    border-radius: $radius-def;
    border: $border-def;
    box-shadow: 0 0.1rem 1rem $color-shadow;
    padding: 1rem;

    .left-panel, .right-panel, .spaced-panel {
        display: flex;
        flex-direction: row;
        align-items: stretch;
    }

    .left-panel {
        justify-content: flex-start;
    }

    .right-panel {
        justify-content: flex-start;
        flex-direction: row-reverse;
    }

    .spaced-panel {
        justify-content: space-between;
    }

    .product-title , .product-description, .product-screenshot {
        margin: 0.5rem;
    }

    .product-title {
        width: 100%;
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        align-items: center;
        margin: 1rem;
        padding: 1rem;
        background-color: $color-light-gray;
        border: $border-def;
        border-radius: $radius-def;
        box-shadow: 0 0.1rem 1rem $color-light-gray inset, 0 0.1rem 1rem $color-gray;

        h2 {
            margin: 0;
            padding: 0;
            color: white;
        }
    }

    .product-description {
        background-color: $color-lightest-gray;
        border: $border-def;
        border-radius: $radius-def;
        padding: 1rem;
        margin: 1rem;
        box-shadow: 0 0 1rem $color-gray inset;

        p {
            margin: 1rem;
            color: white;
        }
    }

    .product-screenshot {
        margin: 1rem;

        img {
            border: $border-def;
            border-radius: $radius-def;
            width: 15rem;
            height: auto;
            cursor: zoom-in;
        }

        .product-screenshot__modal {
            position: fixed;
            left: 0;
            top: 0;
            width: 100vw;
            height: 100vh;
            display: flex;
            justify-content: center;
            align-items: center;
            background-color: #000a;
            z-index: 1040;
            cursor: zoom-out;

            img {
                cursor: zoom-out;
                width: auto;
                height: auto;
                max-width: 100vw;
                max-height: 100vh;
                border: none;
                border-radius: 0;
            }
        }
    }

    .red {
        .product-title {
            background-color: $color-red;
        }
    }

    .blue {
        .product-title {
            background-color: $color-blue;

            h2 {
                color: $color-light-gray;
            }
        }
    }
}