$color-lightest-gray: #484c54;
$color-light-gray: #383c44;
$color-gray: #282c24;
$color-dark-gray: #181c14;
$color-shadow: #383c44;
$color-red: #FF6360;
$color-blue: #61dafb;
$radius-def: 1rem;
$border-def: 1px solid $color-gray;

.home-contact-container {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: stretch;
    margin: auto;
    padding: 1rem 1rem;
    width: 40rem;    
    .contact-panel {
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: stretch;
        margin: 1rem;
        padding: 0.5rem;
        background-color: $color-light-gray;
        border: $border-def;
        border-radius: $radius-def;
        box-shadow: 0 0.1rem 1rem $color-light-gray inset, 0 0.1rem 1rem $color-gray;
        
        h1 {
            margin: 0.5rem;
            color: $color-red;
        }

        p {
            margin: 0.5rem;
            color: white;
        }

        input[type=submit] {
            margin: 0.5rem;
            padding: 1rem 3rem;
            background-color: $color-red;
            border: $border-def;
            border-radius: $radius-def;
            box-shadow: 0 0.1rem 1rem $color-light-gray inset;
            color: white;
            font-size: larger;
            font-weight: bolder;
        }

        textarea {
            margin: 0.5rem;
            padding: 1rem;
            min-height: 20rem;
            border: $border-def;
            border-radius: $radius-def;
            box-shadow: 0 0.1rem 1rem $color-light-gray inset;
            color: $color-lightest-gray;
            text-align: left;
        }
    }

    .f-end {
        display: flex;
        flex-direction: row;
        justify-content: flex-end;
    }
}