$color-lightest-gray: #484c54;
$color-light-gray: #383c44;
$color-gray: #282c24;
$color-dark-gray: #181c14;
$color-shadow: #383c44;
$color-red: #FF6360;
$color-blue: #61dafb;
$radius-def: 1rem;
$border-def: 1px solid $color-gray;

.home-about-container {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: stretch;
    margin: auto;
    padding: 1rem 1rem;
    width: 60rem;    
    .about-panel {
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: stretch;
        margin: 1rem;
        padding: 1rem;
        background-color: $color-light-gray;
        border: $border-def;
        border-radius: $radius-def;
        box-shadow: 0 0.1rem 1rem $color-light-gray inset, 0 0.1rem 1rem $color-gray;

        h2 {
            margin: 0.5rem;
            color: $color-red;
        }
        p {
            margin: 0.5rem ;
            color: white;
        }
    }
}